.section {
    width: 600px;
    height: 200px;
    padding-top: 30px;
    text-align: center;
    display: flex;
    flex-direction: column;
}
.title {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    flex-grow: 1;
    margin: 0;
    background-color: beige;
    font-size: 40px;
    color: brown;
}

.list {
    display: flex;
    list-style: none;
    text-align: center;
    justify-content: space-between;
    padding: 0;
    margin: 0;
}

.item {
    width: 100%;
    padding: 30px 0px;
}